.information {
    height: 330px;
    font-size: 0;
    margin-bottom: 50px;
    // margin-bottom: 25px;
    // background-color: aquamarine;
    // height: 100%;
    .footer_content {
        height: 100%;
        position: relative;
        // background-color: white;
        top: 68%;
        transform: translateY(-50%);
        border-radius: 0 0 15px 15px;
        text-align: center;
        // box-shadow: 0px 0px 30px red;
        // box-shadow: 0px 0px 20px black;
        .col1 {
            position: relative;
            display: inline-block;
            width: 20%;
            height: 65%;
            // border: solid 2px red;
            vertical-align: top;
            top: 20px;
            @include mq (0,945px) {
                width: 32%;
            }
            @include mq (0,620px) {
                width: 80%;
                display: block;
                margin: 0 auto;
            }
            h1 {
                font-family: $fontf01;;
                font-size: 25px;
                padding: 10px;
            }
            a {
                text-decoration: none;
                font-family: $fontf01;;
                font-size: 20px;
                line-height: 1.3;
                // padding:0 0 18px 18px;
                // line-height: 25px;
            }
        }
        
        .col1:first-child {
            @include mq (0,620px) {
                border-bottom: 1px solid lightgrey;
            }
        }
        
        .col1:nth-child(2), .col1:nth-child(3) {
            border-left: 1px solid lightgrey;
            @include mq(0,620px) {
                border-left: none;
                border-bottom: 1px solid lightgrey;
            }
        }
        .col1:nth-child(3) {
            // width: 26%;
            border-left: 1px solid lightgrey;
            @include mq(0,620px) {
                border: none;
            }
        }
    }
}