.basketpage {
    .wrapper {
        position: relative;
        background-color: #fff;
        width: 100%;
        // height: 1000px;
        border-radius: 15px;
        margin-bottom: 50px;
        box-shadow: 0px 0px 20px black;
        h1 {
            font-size: 30px;
            font-weight: 700;
            font-family: $fontf01;
            text-align: center;
            padding: 40px 0 40px 0;
        }
        .prodlist {
            // height: 400px;
            .prod {
                height: 100px;
                width: 90%;
                position: relative;
                margin: 0 auto;
                margin-bottom: 20px;
                background-color: lightgray;
                .prodimg {
                    height: 100%;
                    display: inline-block;
                    img {
                        height: 100%;
                    }
                }
                .prodinfo {
                    display: inline-block;
                    vertical-align: top;
                    height: 100%;
                    div {
                        position: relative;
                        top: 40%;
                    }
                    div:nth-child(2) {
                        display: inline-block;
                    }
                    div:nth-child(3) {
                        display: inline-block;
                    }
                }
            }
        }
    }
}